import React from 'react'
import { bindActionCreators } from 'redux'//creator
import { connect } from 'react-redux'

class App extends React.Component{
    constructor(props,context){
        super(props,context);
        this.state={
            // 
            isFetching:true
        }
    }
    render(){
        return(
            <div>
                {
                    this.state.isFetching?<h1></h1>:<div>{this.props.children}</div>
                }
            </div>
        )
    }
    componentDidMount(){
        this.setState({isFetching:false})
    }
}

//----------------react-redux---------------------
function mapStateToProps(state){
    return {}
}

function mapDispatchToProps(dispatch){
    return {   }
}
export default connect(
    mapStateToProps,
    mapDispatchToProps
)(App)


